@font-face {
  font-family: 'Ubuntu';
  src: url('../fonts/Ubuntu-Regular.woff2') format('woff2'), url('../fonts/Ubuntu-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Ubuntu';
  src: url('../fonts/Ubuntu-Bold.woff2') format('woff2'), url('../fonts/Ubuntu-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}
@font-face {
  font-family: 'Ubuntu';
  src: url('../fonts/Ubuntu-Light.woff2') format('woff2'), url('../fonts/Ubuntu-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}
@font-face {
  font-family: 'Ubuntu';
  src: url('../fonts/Ubuntu-Medium.woff2') format('woff2'), url('../fonts/Ubuntu-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Ubuntu';
  src: url('../fonts/Ubuntu-SemiBold.woff2') format('woff2'), url('../fonts/Ubuntu-SemiBold.woff') format('woff');
  font-weight: 550;
  font-style: normal;
}
.skotch-wrap {
  font-family: "Ubuntu";
}
.img-fluid {
  display: block;
  width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.main-screen {
  position: relative;
}
.main-screen-bg {
  height: 700px;
  background-position: center;
}
.main-screen-bg.center-half-bot {
  background-position: center 75%;
}
.main-screen-bg.center-top {
  background-position: center 20%;
}
.main-screen-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
}
.main-screen-logo img {
  width: 100%;
}
.skotch-menu {
  height: 80px;
  display: flex;
  justify-content: space-between;
  padding: 5px 40px;
  align-items: center;
  background: #fff;
}
.skotch-menu .sign {
  text-transform: uppercase;
  font-weight: bold;
  font-size: 20px;
}
.skotch-menu nav ul {
  display: flex;
  list-style: none;
  padding: 0;
}
.skotch-menu nav ul li {
  border-right: 1px solid #d8d8d8;
  padding: 0 15px;
}
.skotch-menu nav ul li:last-of-type {
  border-right: none;
}
.skotch-menu nav ul li a {
  color: #262626;
  font-size: 20px;
  transition: ease 0.3s;
  border-bottom: 1px solid transparent;
  font-weight: 500;
}
.skotch-menu nav ul li a:hover {
  border-color: #a53030;
}
.skotch-menu .social-links {
  display: flex;
  width: 151px;
  justify-content: flex-end;
}
.skotch-menu .social-links a {
  display: block;
  width: 30px;
  height: 30px;
  background-repeat: no-repeat;
  margin-right: 20px;
}
.skotch-menu .social-links a:last-of-type {
  margin-right: 0;
}
.skotch-menu .social-links .icon-youtube {
  background-image: url('../img/youtube.svg');
  width: 42px;
}
.skotch-menu .social-links .icon-instagram {
  background-image: url('../img/instagram.svg');
}
.about-us {
  background: #f0f0f0;
}
.about-us-wrap {
  max-width: 600px;
  margin: 0 auto;
  padding: 40px 0;
  text-align: center;
}
.about-us-title {
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 30px;
}
.about-us-text {
  line-height: 2em;
}
.works {
  background: #f0f0f0;
  padding: 30px 0;
}
.works-wrap {
  max-width: 600px;
  margin: 0 auto;
}
.works-title {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.works-item {
  display: block;
  height: 250px;
  background: #fff;
  color: #262626;
  margin: 20px 0;
  box-shadow: 0 0 30px 0px rgba(0, 0, 0, 0.25);
  outline: none;
}
.works-item::before {
  content: '';
  display: block;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  background: url('../img/youtube.svg') no-repeat;
}
.works-item span {
  display: block;
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  text-align: center;
}
.works-carousel .owl-stage {
  margin-left: -40px;
}
.contacts {
  background: #fff;
  padding: 30px 0 50px;
}
.contacts-wrap {
  max-width: 600px;
  margin: 0 auto;
  color: #262626;
}
.contacts-top {
  display: flex;
  justify-content: space-around;
  line-height: 1.3em;
  margin-bottom: 30px;
}
.contacts-left a {
  color: #a53030;
}
.contacts .year {
  text-align: center;
}
.pd-wrap {
  max-width: 600px;
  margin: 0 auto;
  padding: 40px 0;
}
.pd-title {
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 30px;
}
.pd-item {
  box-shadow: 0 0 30px 0px rgba(0, 0, 0, 0.35);
  margin: 20px;
}
.pd-item img {
  width: 100%;
  display: block;
}
.works-2 {
  background: #f0f0f0;
  padding: 30px 0;
}
.works-2-wrap {
  max-width: 600px;
  margin: 0 auto;
}
.works-2-title {
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.works-2-item {
  box-shadow: 0 0 30px 0px rgba(0, 0, 0, 0.35);
  width: 100%;
  height: 300px;
  display: block;
  margin-bottom: 30px;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
}
.works-2-item::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: radial-gradient(white, black);
  opacity: 0.5;
  transition: all 0.3s;
}
.works-2-item::after {
  content: '';
  display: block;
  width: 50px;
  height: 30px;
  background: url('../img/youtube-red.svg') no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.works-2-item:hover::before {
  opacity: 0.3;
}
.works-2-item.fortex {
  background-image: url('../img/preview-fortex.png');
}
.works-2-item.megabonus {
  background-image: url('../img/preview-megabonus.png');
  background-position: 50% -40px;
}
